.TodoList {
  background-color: rgb(61, 161, 156);
  width:300px;
  height:50px;
  margin: 20px auto;
  border-radius: 5px;
  color:white;
}

.TodoList p {
  
  padding:10px;
  
  position: relative;

}

.TodoList p span {
  position: absolute;
  right: 10px;
  cursor:pointer;
}

.faicons {
  margin:0 5px;
  font-size:15px;
}

.TodoList p input[type= text] {
  background-color: transparent;
  color:white;
  border:0;
  font-size:18px;
  /* padding:10px; */
  margin-left: 30px;

}
.TodoList p input:focus {
  outline: none;
}

.TodoList p input[type = checkbox]{
  position: absolute;
  cursor:pointer;
  width:20px;
  height: 20px;
}

.TodoList p input[type = checkbox]:checked {
  background: rgb(230, 179, 13);
}