.TodoForm input{
  background-color: rgb(16, 48, 46);

    height:50px;
    width:200px;
    margin:30px;
    padding:0 20px;
    border: 0;
    font-size: 18px;
    color: white;
    border-radius:7px;
}

.TodoForm input:focus, .TodoForm button:focus{
  outline:none;
}

.TodoForm input::placeholder {
  color: rgba(108, 196, 190, 0.5);
}

.TodoForm button {
  height:50px;
  width:80px;
  margin:10px;
  border:none;
  border-radius: 5px;
  background-color: rgb(202, 158,24);
  cursor: pointer;
  color: white;
  font-weight:bolder;
  font-size:13px;

}


